import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EmailPage } from '../email/email';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';

/**
 * Generated class for the SignupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {
  private cadastro: FormGroup;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private formBuilder: FormBuilder) {

    this.cadastro = this.formBuilder.group({
      nome: ['', Validators.compose([Validators.maxLength(30), Validators.required])],
      nascimento: ['', Validators.required],
      sexo: ['', Validators.required],
      tipoSanguineo: ['', Validators.required],
      email: ['', Validators.compose([Validators.email, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$'), Validators.required])],
      senha: ['', Validators.required]
    });

  }
  
  dadosUsuario(){
    console.log(this.cadastro.value);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
  }

  abrirEmail() {
    this.navCtrl.push(EmailPage);
  }

}



